/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  font-weight: 300;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

main {
  flex: 1;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
}

h1, h2 {
  font-weight: 400;
}

h1 {
  font-size: 4.4rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 2.4rem;
  font-weight: 300;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
  color: #000;
}

p {
  font-size: 1.6rem;
  line-height: 1.8;
}

.header {
  padding: 4rem 0;
  background-color: #6D94FD;
}
.header.imagen {
  background-image: url(../img/header.jpg);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.imagen {
    min-height: 60rem;
  }
}
.header__titulo {
  line-height: 2;
  margin-top: 8rem;
  text-align: center;
  color: #FFFFFF;
  font-weight: bold;
  font-size: 2.5rem;
}
@media (min-width: 768px) {
  .header__titulo {
    margin-top: unset;
    text-align: left;
    max-width: 60rem;
    font-size: 4rem;
  }
}
.header__contenido {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .header__contenido {
    min-height: 50rem;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
  }
}

.logo {
  width: 30rem;
}
@media (min-width: 768px) {
  .logo {
    max-width: 45rem;
  }
}

@media (min-width: 768px) {
  .nav {
    flex-direction: row;
    justify-content: space-between;
  }
}
.nav__enlace {
  display: block;
  text-align: center;
  margin: 1rem 0;
  font-weight: bold;
  font-size: 1.9rem;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .nav__enlace {
    display: inline-block;
    margin: unset;
    margin-right: 2rem;
  }
}
.nav__enlace:hover {
  color: #78E0FA;
}

.footer {
  background-color: #6D94FD;
}
.footer__contenido {
  padding-top: 2rem;
}
.footer__copy {
  color: #FFFFFF;
  font-size: 2rem;
  text-align: center;
  padding: 1rem;
  margin: 0;
  font-weight: bold;
}

.categorias {
  margin: 2rem 0;
}
.categorias__titulo {
  text-align: center;
  color: #011e6a;
  margin: 2rem 0;
  text-transform: uppercase;
  font-weight: 300;
}
@media (min-width: 768px) {
  .categorias__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.categoria {
  text-align: center;
  padding: 1rem;
}
@media (min-width: 768px) {
  .categoria {
    padding: 2rem;
  }
}
.categoria__maks {
  overflow: hidden;
}
.categoria__img {
  margin: 0 auto;
  width: 12rem;
  height: 12rem;
}
.categoria__img:hover {
  animation-name: rotarIcono;
  animation-duration: 2s;
  animation-timing-function: ease-in-out;
}
.categoria__titulo {
  margin: 2rem 0;
  color: #033ace;
  text-transform: uppercase;
  font-size: 2.3rem;
}

@keyframes rotarIcono {
  0% {
    transform: rotate(0deg);
  }
  25% {
    transform: rotate(90deg);
  }
  50% {
    transform: rotate(180deg);
  }
  75% {
    transform: rotate(270deg);
  }
  100% {
    transform: rotate(360deg);
  }
}